import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { images } from "../config";
import { loginSchema } from "../validation";
import { Button } from "../components/button";
import { useNavigate, useLocation } from "react-router-dom";
import { authService } from "../services";
import { useState } from "react";

export const Login = () => {
  const [loading, setLoading] = useState(false);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(loginSchema),
  });

  const navigate = useNavigate();
  const location = useLocation();
  let from = location.state?.from?.pathname || "/";

  const onSubmit = async (formData) => {
    try {
      setLoading(true);
      const {
        data: { data },
      } = await authService.login(formData);
      localStorage.setItem("accessToken", data.access_token);
      localStorage.setItem("user", data.user);
      setLoading(false);
      reset();
      navigate(from, { replace: true });
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
  };

  return (
    <div className="mx-auto md:h-screen flex flex-col justify-center items-center px-6 pt-8 pt:mt-0">
      <a
        href="#"
        className="text-2xl font-semibold flex justify-center items-center mb-8 lg:mb-10"
      >
        <img src={images.logo} className="h-10 mr-4" alt="logo" />
      </a>

      <div className="bg-white shadow rounded-lg md:mt-0 w-full sm:max-w-screen-sm xl:p-0">
        <div className="p-6 sm:p-8 lg:p-16 space-y-8">
          <h2 className="text-2xl lg:text-3xl font-bold text-gray-900">
            Sign in
          </h2>

          <form className="mt-8 space-y-6" onSubmit={handleSubmit(onSubmit)}>
            <div>
              <label
                htmlFor="email"
                className="text-sm font-medium text-gray-900 block mb-2"
              >
                Email
              </label>
              <input
                {...register("email")}
                type="email"
                name="email"
                id="email"
                className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-cyan-600 focus:border-cyan-600 block w-full p-2.5"
                placeholder="name@company.com"
              />
              {errors.email?.message && (
                <div className="text-sm text-red-600 mt-1">
                  {errors.email?.message}
                </div>
              )}
            </div>
            <div>
              <label
                htmlFor="password"
                className="text-sm font-medium text-gray-900 block mb-2"
              >
                Password
              </label>
              <input
                {...register("password")}
                type="password"
                name="password"
                id="password"
                placeholder="••••••••"
                className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-cyan-600 focus:border-cyan-600 block w-full p-2.5"
              />
              {errors.password?.message && (
                <div className="text-sm text-red-600 mt-1">
                  {errors.password?.message}
                </div>
              )}
            </div>
            <div className="flex items-start">
              <a
                href="#"
                className="text-sm text-blue-600 hover:underline ml-auto"
              >
                Forgot Password?
              </a>
            </div>
            <Button
              type="submit"
              disabled={loading}
              className="bg-blue-600 hover:bg-blue-700 focus:bg-blue-700 active:bg-blue-800 inline-block px-6 py-2.5 text-white font-medium text-xs leading-tight uppercase rounded shadow-md  hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0  active:shadow-lg transition duration-150 ease-in-out"
            >
              Login
            </Button>
          </form>
        </div>
      </div>
    </div>
  );
};
