import { useState } from "react";
import { Navbar, Siderbar, Footer } from ".";

export function Layout({ children }) {
  const [showSidebar, setShowSidebar] = useState(false);
  const [showBackDrop, setShowBackDrop] = useState(false);
  const [showSidebarMobileHamburger, setShowSidebarMobileHamburger] =
    useState(true);
  const [showSidebarMobileClose, setShowSidebarMobileClose] = useState(false);

  const toggleSidebarMobile = () => {
    setShowSidebar((prev) => !prev);
    setShowBackDrop((prev) => !prev);
    setShowSidebarMobileHamburger((prev) => !prev);
    setShowSidebarMobileClose((prev) => !prev);
  };

  return (
    <>
      <Navbar
        toggleSidebarMobile={toggleSidebarMobile}
        showSidebarMobileHamburger={showSidebarMobileHamburger}
        showSidebarMobileClose={showSidebarMobileClose}
      />
      <div className="flex overflow-hidden bg-white pt-16">
        <Siderbar
          showSidebar={showSidebar}
          showBackDrop={showBackDrop}
          toggleSidebarMobile={toggleSidebarMobile}
        />
        <div
          id="main-content"
          className="h-full w-full bg-gray-50 relative overflow-y-auto lg:ml-64"
        >
          {children}
          <Footer />
        </div>
      </div>
    </>
  );
}
