export function Button({
  type,
  title,
  danger,
  onClick,
  outlined,
  color,
  isLoading,
  className,
  disabled,
  children,
  ...restProps
}) {
  return (
    <button
      {...restProps}
      type={type ? type : "button"}
      disabled={disabled}
      onClick={onClick}
      className={` disabled:${
        !disabled ? "cursor:pointer" : "cursor-not-allowed"
      }   ${className}`}
    >
      {children}
    </button>
  );
}
